import React,{Component} from 'react';
import HomeHeader from '../../components/HomeHeader/index'
import Slider from '../../components/Slider/index'
import Ad from './subpage/Ad.js';
import List from './subpage/List'
import {connect} from 'react-redux';

class Home extends Component{

    render(){
        return (
            <div>
              <HomeHeader history={this.props.history} cityName={this.props.userInfo.cityName}/>
              <Slider/>
              <Ad/>
              <List cityName={this.props.userInfo.cityName}/>
            </div>
        )
    }
    componentWillMount(){
        console.log(this.props);
    }
}

// 取出redux 中的城市传递给 HomeHeader 组件

export default connect(
    state=>{
        return {userInfo:state.userInfo} //取出redux state中userInfo的数据，赋予给变量 userInfo
    }
)(Home);



















